<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-自定义更多样式1" class="vue-doc-element">自定义更多样式</h2>
<p class="vue-doc-element">通过自定义slot和丰富的配置，可以组合出很多统计效果。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Circle
        :size="250"
        :trail-width="4"
        :stroke-width="5"
        :percent="75"
        stroke-linecap="square"
        stroke-color="#43a3fb">
        <div class="demo-Circle-custom">
            <h1>42,001,776</h1>
            <p>消费人群规模</p>
            <span>
                总占人数
                <i>75%</i>
            </span>
        </div>
    </Circle>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue">&lt;<span class="hljs-built_in">style</span> lang=<span class="hljs-string">"less"</span>&gt;
    .<span class="hljs-built_in">demo</span>-Circle-custom{
        &amp; h1{
            <span class="hljs-built_in">color</span>: #3f414d;
            <span class="hljs-built_in">font</span>-size: 28px;
            <span class="hljs-built_in">font</span>-weight: normal;
        }
        &amp; p{
            <span class="hljs-built_in">color</span>: #<span class="hljs-number">657180</span>;
            <span class="hljs-built_in">font</span>-size: 14px;
            margin: 10px <span class="hljs-number">0</span> 15px;
        }
        &amp; span{
            <span class="hljs-built_in">display</span>: <span class="hljs-built_in">block</span>;
            padding-top: 15px;
            <span class="hljs-built_in">color</span>: #<span class="hljs-number">657180</span>;
            <span class="hljs-built_in">font</span>-size: 14px;
            &amp;:before{
                <span class="hljs-built_in">content</span>: '';
                <span class="hljs-built_in">display</span>: <span class="hljs-built_in">block</span>;
                <span class="hljs-built_in">width</span>: 50px;
                <span class="hljs-built_in">height</span>: 1px;
                margin: <span class="hljs-number">0</span> auto;
                <span class="hljs-built_in">background</span>: #e0e3e6;
                <span class="hljs-built_in">position</span>: relative;
                top: -15px;
            };
        }
        &amp; span i{
            <span class="hljs-built_in">font</span>-<span class="hljs-built_in">style</span>: normal;
            <span class="hljs-built_in">color</span>: #3f414d;
        }
    }
&lt;/<span class="hljs-built_in">style</span>&gt;
&lt;template&gt;
    &lt;Circle
        :size=<span class="hljs-string">"250"</span>
        :trail-<span class="hljs-built_in">width</span>=<span class="hljs-string">"4"</span>
        :stroke-<span class="hljs-built_in">width</span>=<span class="hljs-string">"5"</span>
        :percent=<span class="hljs-string">"75"</span>
        stroke-linecap=<span class="hljs-string">"square"</span>
        stroke-<span class="hljs-built_in">color</span>=<span class="hljs-string">"#43a3fb"</span>&gt;
        &lt;div class=<span class="hljs-string">"demo-Circle-custom"</span>&gt;
            &lt;h1&gt;<span class="hljs-number">42</span>,<span class="hljs-number">001</span>,<span class="hljs-number">776</span>&lt;/h1&gt;
            &lt;p&gt;消费人群规模&lt;/p&gt;
            &lt;span&gt;
                总占人数
                &lt;i&gt;<span class="hljs-number">75</span><span class="hljs-symbol">%</span>&lt;/i&gt;
            &lt;/span&gt;
        &lt;/div&gt;
    &lt;/Circle&gt;
&lt;/template&gt;
&lt;script&gt;
    export default {

    }
&lt;/script&gt;


</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],

    }
</script>
  <style lang="less">
    .demo-Circle-custom{
        & h1{
            color: #3f414d;
            font-size: 28px;
            font-weight: normal;
        }
        & p{
            color: #657180;
            font-size: 14px;
            margin: 10px 0 15px;
        }
        & span{
            display: block;
            padding-top: 15px;
            color: #657180;
            font-size: 14px;
            &:before{
                content: '';
                display: block;
                width: 50px;
                height: 1px;
                margin: 0 auto;
                background: #e0e3e6;
                position: relative;
                top: -15px;
            };
        }
        & span i{
            font-style: normal;
            color: #3f414d;
        }
    }
</style>
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


